import React, {useEffect} from 'react';

function Filter({setActiveFilter, activeFilter, setFiltered, products}) {

    useEffect(() => {
        if (activeFilter === "0") {
            setFiltered(products)
            return
        }
        const filtered = products.filter((product) =>
            product.category.includes(activeFilter)
        )
        setFiltered(filtered)
    }, [activeFilter])

    return (
        <div className="filter-container">
            <button className={activeFilter === "0" ? "active product-filter" : "product-filter"} onClick={() => setActiveFilter("0")}> </button>
            <button className={activeFilter === "" ? "active product-filter" : "product-filter"} onClick={() => setActiveFilter("")}></button>
            <button className={activeFilter === "" ? "active product-filter" : "product-filter"} onClick={() => setActiveFilter("")}></button>
            <button className={activeFilter === "" ? "active product-filter" : "product-filter"} onClick={() => setActiveFilter("")}></button>
            <button className={activeFilter === "" ? "active product-filter" : "product-filter"} onClick={() => setActiveFilter("")}></button>
        </div>
    );
}

export default Filter;