import React from 'react';

import {Container} from "react-bootstrap";

import {motion} from "framer-motion";
import {animationStart, reveal} from "../utils/animation";

function SupportScreen() {
    return (
        <motion.div>
            <Container>
                <motion.div
                    variants={reveal}
                    initial='hiddenVariantY'
                    animate='revealedVariantY'
                    transition={{
                        ease: 'easeIn',
                        type: 'spring',
                        staggerChildren: .1,
                        duration: 1,
                        delayChildren: animationStart,
                        delay: animationStart
                    }}
                    className='text-center mb-3'
                >
                    <motion.h1 variants={reveal}></motion.h1>
                </motion.div>

                <motion.div
                    variants={reveal}
                    initial='hiddenVariantX'
                    animate='revealedVariantX'
                    transition={{
                        ease: 'easeIn',
                        type: 'spring',
                        staggerChildren: .1,
                        duration: 1,
                        delayChildren: animationStart + .25,
                        delay: animationStart
                    }}
                    className='text-left'
                >
                    <motion.div variants={reveal}><strong className='mx-5'>E-MAIL: </strong>support@unfort.ru</motion.div>
                    <motion.div variants={reveal}><strong className='mx-5'> : </strong>- - 10:00  21:00 -  Unfort</motion.div>
                    <motion.div variants={reveal}><strong className='mx-5'>: </strong>  </motion.div>
                    <motion.div variants={reveal}><strong className='mx-5'>: </strong>860330317687</motion.div>
                    <motion.div variants={reveal}><strong className='mx-5'>: </strong>322861700084485</motion.div>
                    <motion.div variants={reveal}><strong className='mx-5'>: </strong> " "</motion.div>
                    <motion.div variants={reveal}><strong className='mx-5'>: </strong>044525974</motion.div>
                    <motion.div variants={reveal}><strong className='mx-5'> : </strong>40802810600003939678</motion.div>
                    <motion.div variants={reveal}><strong className='mx-5'>. : </strong>30101810145250000974</motion.div>
                </motion.div>
            </Container>
        </motion.div>
    );
}

export default SupportScreen;